import React from 'react'
import './Header.css'

function Header() {

    function showTable(val) {
        val = '.' + val
        document.querySelector(val).style.display = 'block'
    }

    return(
      <div className="sidenav">
        <div className="content">
          <button className="dropdown-btn">Main</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('secundarios')}>Itens Secundários</button>
          </div>
          <button className="dropdown-btn">Kits</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('grid-tie')}>Kits Grid-Tie</button>
            <button onClick={() => showTable('off-grid')}>Kits Off-grid</button>
            <button onClick={() => showTable('bomba')}>Bomba Solar</button>
          </div>
          <button className="dropdown-btn">Painéis</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('painel-disponivel')}>Paineis Disponiveis</button>
            <button onClick={() => showTable('painel-indisponivel')}>Paineis Indisponiveis</button>
          </div>
          <button className="dropdown-btn">Inversores Grid-Tie</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('inversorGT-disponivel')}>Inversores Disponiveis</button>
            <button onClick={() => showTable('inversorGT-indisponivel')}>Inversores Grid-Tie Indisponiveis</button>
          </div>
          <button className="dropdown-btn">Inversores Off-Grid</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('inversorOG-disponivel')}>Inversores Grid-Tie Disponiveis</button>
            <button onClick={() => showTable('inversorOG-indisponivel')}>Inversores Off-Grid Indisponiveis</button>
          </div>
          <button className="dropdown-btn">Controladores</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('controlador-disponivel')}>Controladores Off-Grid Disponiveis</button>
            <button onClick={() => showTable('controlador-indisponivel')}>Controladores Indisponiveis</button> 
          </div>
          <button className="dropdown-btn">Bombas Solares</button>
          <div className="dropdown-container">
            <button onClick={() => showTable('bomba-disponivel')}>Bombas Disponiveis</button> 
            <button onClick={() => showTable('bomba-indisponivel')}>Bombas Indisponiveis</button> 
          </div>
        </div>
      </div>
    )
}

export default Header

