import React, { Component } from 'react'
import './tables.css'

export default class Tables extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            productIsLoaded: false,
            productList: [],
        }
    }
    

    componentDidMount() {  
        fetch('https://spartt.github.io/json/productsData.json')
        .then(resp => resp.json())
        .then(
            (result) => {
                this.setState({
                    productIsLoaded: true,
                    productList: result,
                });
            },
            (error) => {
                this.setState({
                    productIsLoaded: true,
                    error
                });
            }
            
        )                
    }

    closeTable(val) {
        val = '.' + val
        document.querySelector(val).style.display = 'none'
    }

    renderProducts(param) {
        const { error, productIsLoaded, productList } = this.state 
        //console.log(productList)
        if(error) {
            return(<tr><td>Error: {error.message}</td></tr>)
        }else if( !productIsLoaded) {
            return <tr><td colSpan="2">Loading...</td></tr>;
        }else {
            var selectedList
            switch(param) {
                case 'inversorGT.disponiveis':
                    selectedList = productList.inversorGT.disponiveis
                break;
                case 'inversorGT.indisponiveis':
                    selectedList = productList.inversorGT.indisponiveis
                break;
                case 'inversorOG.disponiveis':
                    selectedList = productList.inversorOG.disponiveis
                break;
                case 'inversorOG.indisponiveis':
                    selectedList = productList.inversorOG.indisponiveis
                break;
                case 'controlador.disponiveis':
                    selectedList = productList.controlador.disponiveis
                break;
                case 'controlador.indisponiveis':
                    selectedList = productList.controlador.indisponiveis
                break;
                case 'bomba.disponiveis':
                    selectedList = productList.bomba.disponiveis
                break;
                case 'bomba.indisponiveis':
                    selectedList = productList.bomba.indisponiveis
                break;
                case 'painel.disponiveis':
                    selectedList = productList.painel.disponiveis
                break;
                case 'painel.indisponiveis':
                    selectedList = productList.painel.indisponiveis
                break;
                default:
                    //console.log('parametro não encontrado')
                break;
            }   
            return(
                selectedList.map(product => (
                <tr key={product.productId} >
                    <td>{product.productId}</td>
                    <td>{product.nome}</td>
                    <td>{product.pag}</td>
                </tr>
                ))
            )
        }
    }

    render() {
        return(
            <div className="products-container">

                <div className={`content ${this.props.class}`}>
                    <h2>{this.props.titulo}</h2>
                    <span onClick={() => this.closeTable(this.props.class)} className="closeBtn">X</span>
                        <table style={{marginTop: 4+'ch'}}>
                        <tbody>
                            <tr className="tableTitle"><th>Prod Id</th><th>Nome</th><th>Pag</th></tr>
                            {this.renderProducts(this.props.param)}
                        </tbody>
                        </table>
                </div>
            </div>
        )
    }

}