import React, { Component } from 'react'
import './toActivate.css'

export default class kitDetails extends Component {
    
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      kitIsLoaded: false,
      kitsList: [],
      productIsLoaded: false,
      productList: []
    }
}

	componentDidMount() {  

    fetch('https://spartt.github.io/json/kitsData.json')
    .then(resp => resp.json())
    .then(
      (result) => {
        this.setState({
          kitIsLoaded: true,
           kitsList: result
        });
      },
        (error) => {
          this.setState({
            kitIsLoaded: true,
            error
        	});
        } 
    )
        
    fetch('https://spartt.github.io/json/productsData.json')
    .then(resp => resp.json())
    .then(
      (result) => {
        this.setState({
          productIsLoaded: true,
          productList: result,
        });
      },
      (error) => {
        this.setState({
          productIsLoaded: true,
          error
        });
      }        
		)
		 
  }

	// retorna prod "primario" q n ta em nenhum kit
    alg(arrProd, arrKit) {

        var copyArr = arrProd
				var prodEmKit = []

        if(arrKit.length === 0) {
          return(
            <tr><td style={{textAlign: "center"}} colSpan="2">Não há kits ativos nessa categoria</td></tr>
          )
				}

        for(let i = 0; i < arrKit.length; i++ ) {

          arrKit[i].products.forEach(function(el) {

            if( (el.nome.toLowerCase().search(/inversor/g) > -1 && el.nome.toLowerCase().search(/grid-tie/g) > -1 ) 
              || (el.nome.toLowerCase().search(/micro\sinversor\sgrid-tie/g) > -1) ||  (el.nome.toLowerCase().search(/inversor\shibrido/g) > -1) 
              || (el.nome.toLowerCase().search(/onda\ssenoidal/g) > -1) || (el.nome.toLowerCase().search(/onda\smodificada/g) > -1) || (el.nome.toLowerCase().search(/inversor.+carregador/g))
              || (el.nome.toLowerCase().search(/inverter.+charger/g)) ) {
                prodEmKit.push(el.nome)
            }else if(el.nome.toLowerCase().search(/bomba\ssolar/g) > -1) {
              prodEmKit.push(el.nome)
            }else if (el.nome.toLowerCase().search(/controlador\sde\scarga/g) > -1 || el.nome.toLowerCase().search(/tracer/g) > -1) {
              prodEmKit.push(el.nome)
            }
					})
        }
        for(let i = 0; i < prodEmKit.length; i++) {
            for(let j = 0; j < copyArr.length; j++) {
                if(prodEmKit[i] === copyArr[j].nome) {
                    copyArr.splice(j, 1)
                }
            }
        }
        if(copyArr.length > 0) {
            return(
                    copyArr.map(prod => (
                        <tr key={prod.productId}><td>{prod.productId}</td><td>{prod.nome}</td></tr>
                    ))
            )
        }else {
            return(
                copyArr.map(prod => (
                    <tr key={prod.productId}><td colSpan="2">Não foram encontrados produtos</td></tr>
                ))
            )
        }
    }
    

    renderK() {
        const { error, kitIsLoaded, kitsList, productIsLoaded, productList  } = this.state 
        if(error) {
            return(<div>Error: {error.message}</div>)
        }else if( !productIsLoaded || !kitIsLoaded) {
            return <div>Loading...</div>;
        }else {
            return(
                <div className="ativaveis">
                    <h2>Inversores Grid-Tie Ativaveis</h2>
                    <table style={{marginTop: 2 + 'ch'}}>
                        <tbody>
                            <tr className="tableTitle"><th>Prod Id</th><th>Nome</th></tr>
                            {this.alg(productList.inversorGT.disponiveis, kitsList.gridTie)}
                        </tbody>
                    </table>
                    <h2>Inversores Off-Grid Ativaveis</h2>
                    <table style={{marginTop: 2 + 'ch'}}>
                        <tbody>
                            <tr className="tableTitle"><th>Prod Id</th><th>Nome</th></tr>
                            {this.alg(productList.inversorOG.disponiveis, kitsList.offGrid)}
                        </tbody>
                    </table>
                    <h2>Controladores Ativaveis</h2>
                    <table style={{marginTop: 2 + 'ch'}}>
                        <tbody>
                            <tr className="tableTitle"><th>Prod Id</th><th>Nome</th></tr>
                            {this.alg(productList.controlador.disponiveis, kitsList.offGrid)}
                        </tbody>
                    </table>
                    <h2>Bombas Ativaveis</h2>
                    <table style={{marginTop: 2 + 'ch'}}>
                        <tbody>
                            <tr className="tableTitle"><th>Prod Id</th><th>Nome</th></tr>
                            {this.alg(productList.bomba.disponiveis, kitsList.bomba)}
                        </tbody>
                    </table>
                </div>
            )
        }
    }

    render() {
        return(
            <div>
                {this.renderK()}
            </div>
        )
    }
	}

