import React, { Component } from 'react'
import './kitsEmFalta.css'

export default class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
        error: null,
        kitIsLoaded: false,
        kitsList: [],
    }
}


componentDidMount() {  

  fetch('https://spartt.github.io/json/kitsData.json')
  .then(resp => resp.json())
  .then(
    (result) => {
      this.setState({
        kitIsLoaded: true,
        kitsList: result
      });
    },
    (error) => {
      this.setState({
        kitIsLoaded: true,
        error
      });
    }    
  )                 
}

closeTable(val) {
  val = '.' + val
  document.querySelector(val).style.display = 'none'
}

ProdutosPrincipais(param) {
    const { error, kitIsLoaded, kitsList  } = this.state 
    if(error) {
      return <tbody><tr><td>Error: {error.message}</td></tr></tbody>
    }else if(!kitIsLoaded) {
      return <tbody><tr><td colSpan="2">Loading...</td></tr></tbody>
    }else {
      var selectedList
      var indisponivel = []

      switch(param) {
          case 'gridTie':
              selectedList = kitsList.gridTie

              if(selectedList.length === 0) {
                return <tbody><tr><td>Sem Kits Grid-tie disponíveis</td></tr></tbody>
              }

              selectedList.forEach(function(el, i) {
                var painel = []
                var inversor = []
                var kitId = el.kitId
                el.products.forEach(function(el, i) {
                  if(el.nome.toLowerCase().search(/painel\ssolar\s/g) > -1 && el.nome.toLowerCase().search(/suporte/g) === -1 ){
                    painel.push(el.nome)																								     
                  }else if( (el.nome.toLowerCase().search(/inversor/g) > -1 && el.nome.toLowerCase().search(/grid-tie/g) > -1 ) || (el.nome.toLowerCase().search(/micro\sinversor\sgrid-tie/g) > -1) ) {
                    inversor.push(el.nome)
                  }
                })
                
                if(painel.length === 0) {
                  indisponivel.push(<tr key={`painel ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Painel em estoque: {painel.length}</td></tr>)
                }else if(inversor.length === 0) {
                  indisponivel.push(<tr key={`inversor ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Inversor em estoque: {inversor.length}</td></tr>)
                }
              })
          break;
          case 'offGrid':
              selectedList = kitsList.offGrid

              if(selectedList.length === 0) {
                return <tbody><tr><td>Sem Kits Off-Grid disponíveis</td></tr></tbody>
              }
              selectedList.forEach(function(el, i) {
                var painel = []
                var controlador = []
                var kitId = el.kitId
                console.log(el)
                el.products.forEach(function(el, i) {
                  if(el.nome.toLowerCase().search(/painel\ssolar\s/g) > -1 && el.nome.toLowerCase().search(/suporte/g) === -1 ){
                    painel.push(el.nome)																								     
                  }else if(el.nome.toLowerCase().search(/controlador\sde\scarga/g) >-1) {
                    controlador.push(el.nome)
                  }
                })

                if(painel.length === 0) {
                  indisponivel.push(<tr key={`painel ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Painel em estoque: {painel.length}</td></tr>)
                }else if(controlador.length === 0) {
                  indisponivel.push(<tr key={`controlador ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Controlador em estoque: {controlador.length}</td></tr>)
                } 
      
              })
          break;
          case 'bombaKit':
              selectedList = kitsList.bomba

              if(selectedList.length === 0) {
                return <tbody><tr><td>Sem Kits de Bomba Solar disponíveis</td></tr></tbody>
              }

              selectedList.forEach(function(el, i) {
                var painel = []
                var bomba = []
                var kitId = el.kitId
                el.products.forEach(function(el, i) {
                  if(el.nome.toLowerCase().search(/painel\ssolar\s/g) > -1 && el.nome.toLowerCase().search(/suporte/g) === -1 ){
                    painel.push(el.nome)																								     
                  }else if(el.nome.toLowerCase().search(/bomba\ssolar/g) >-1) {
                    bomba.push(el.nome)
                  }
                })

                if(painel.length === 0) {
                  indisponivel.push(<tr key={`painel ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Painel em estoque: {painel.length}</td></tr>)
                }else if(bomba.length === 0) {
                  indisponivel.push(<tr key={`bomba ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Bomba em estoque: {bomba.length}</td></tr>)
                }
      
              })

          break;
          default:
              console.log('parametro não encontrado')
          break;      
      }

      // fazer checagem de produtos "secundários?"?

      if(indisponivel.length > 0) {
        return(
          <tbody>
            <tr><th>ID</th><th>Link Loja</th><th>Link Admin</th><th>Tipo indisponivel</th></tr>
            {indisponivel}
          </tbody>
        )
      }else {
        return <tbody><tr><td>Nenhum kit possui algum item que está indisponivel</td></tr></tbody>
      }
    }
}

ProdutosSecundarios(param) {

  const { error, kitIsLoaded, kitsList  } = this.state 
  if(error) {
    return <tbody><tr><td>Error: {error.message}</td></tr></tbody>
  }else if(!kitIsLoaded) {
    return <tbody><tr><td colSpan="2">Loading...</td></tr></tbody>
  }else {
    var selectedList
    var indisponivel = []
    
    switch(param) {
      case 'gridTie':
          selectedList = kitsList.gridTie
      break;
      case 'offGrid':
        selectedList = kitsList.offGrid
      break;
      case 'bombaKit':
        selectedList = kitsList.bomba
      break;
      default: 
        console.log('k')
      break;
    }

    selectedList.forEach(function(el, i) {
      var suporte = [],
      cabo = [],
      conector = []

      var kitId = el.kitId
      el.products.forEach(function(el, i) {

        if( (el.nome.toLowerCase().search(/suporte/g) > -1) || (el.nome.toLowerCase().search(/telha/g) > -1) ) {
          suporte.push(el.nome)
        }else if(el.nome.toLowerCase().search(/cabo\s/g) > -1 ){
          cabo.push(el.nome)																								     
        }else if(el.nome.toLowerCase().search(/conector/g) > -1 ) {
          conector.push(el.nome)
        }
      })

      if(cabo.length === 0) {
        indisponivel.push(<tr key={`cabo ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Cabo em estoque: {cabo.length}</td></tr>)
      }
      if(conector.length === 0) {
        indisponivel.push(<tr key={`conector ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Conector em estoque: {conector.length}</td></tr>)
      }
      if(suporte.length === 0) {
        indisponivel.push(<tr key={`suporte ${kitId}`}><td>{kitId}</td><td><a target="blank" rel="noopener" href={ `https://www.minhacasasolar.com.br/listacompra/${kitId}`}>Página da Loja</a></td><td><a target="blank" rel="noopener" href={`http://admin.minhacasasolar.com.br/ListaCompras/Editar/${kitId}`}>Página da Loja</a></td><td>Suporte em estoque: {suporte.length}</td></tr>)
      }

    })

    //console.log(indisponivel)
    if(indisponivel.length > 0) {
      return(
        <tbody>
          <tr><th>ID</th><th>Link Loja</th><th>Link Admin</th><th>Tipo indisponivel</th></tr>
          {indisponivel}
        </tbody>
      )
    }else {
      return <tbody><tr><td>Nenhum kit possui algum item que está indisponivel</td></tr></tbody>
    }

  }

}


  render() {
      return(
      <div className="kit-sem-prod">
        <table>
            {this.ProdutosPrincipais('gridTie')}
        </table>
        <table>
          {this.ProdutosPrincipais('offGrid')}
        </table>
        <table>
          {this.ProdutosPrincipais('bombaKit')}
        </table>

        <div className="secundarios">
        <h2>Produtos secundários</h2>
        <span onClick={() => this.closeTable('secundarios')} className="closeBtn">X</span>
          <table>
            {this.ProdutosSecundarios('gridTie')}
          </table>
        </div>
      </div>
      )
  }

}
