import React, { Component, Fragment } from 'react'

/*

ID
TIPO
PAINEL
INVERSOR OU CONTROLADOR
STRINGBOX OU BATERIA
SUPORTE SE > 230
CABO
CONECTOR

*/

export default class kitDetails extends Component {

    render() {
        if(this.props.tipo === 'grid-tie') {
            return(
                <Fragment>
                <div className="kitDetails">
                    <div style={{display: 'flex'}}><strong>Tipo: </strong> {this.props.tipo}<strong>ID: </strong> {this.props.id}</div>
                    <span><strong>Painel: </strong> {this.props.painel}</span>
                    <span><strong>Inversor: </strong> {this.props.inversor}</span>
                    <span><strong>String Box: </strong> {this.props.stringbox}</span>
                    <span><strong>Suporte: </strong>{this.props.suporte}</span>
                    <span><strong>Cabo: </strong>{this.props.cabo}</span>
                    <span><strong>Conector: </strong>{this.props.conector}</span>
                    <span><strong>Outros: </strong>{this.props.extras}</span>
                </div>
                </Fragment>
            )
        }else if (this.props.tipo === 'off-grid') {
            return(
                <Fragment>
                <div className="kitDetails">
                    <div style={{display: 'flex'}}><strong>Tipo: </strong> {this.props.tipo}<strong>ID: </strong> {this.props.id}</div>
                    <span><strong>Painel: </strong> {this.props.painel}</span>
                    <span><strong>Controlador: </strong> {this.props.controlador}</span>
                    <span><strong>Inversor: </strong> {this.props.inversor}</span>
                    <span><strong>Suporte: </strong>{this.props.suporte}</span>
                    <span><strong>Cabo: </strong>{this.props.cabo}</span>
                    <span><strong>Conector: </strong>{this.props.conector}</span>
                    <span><strong>Outros: </strong>{this.props.extras}</span>
                </div>
                </Fragment>
            )
        }else {
            return(
                <Fragment>
                <div className="kitDetails">
                    <div style={{display: 'flex'}}><strong>Tipo: </strong> {this.props.tipo}<strong>ID: </strong> {this.props.id}</div>
                    <span><strong>bomba: </strong> {this.props.bomba}</span>
                    <span><strong>Suporte: </strong>{this.props.suporte}</span>
                    <span><strong>Cabo: </strong>{this.props.cabo}</span>
                    <span><strong>Conector: </strong>{this.props.conector}</span>
                    <span><strong>Outros: </strong>{this.props.extras}</span>
                </div>
                </Fragment>
            )
        }
    }

} 
