import React, { Component } from 'react'
import './tables.css'
import KitDetails from './kitDetails.js'

export default class Tables extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            kitIsLoaded: false,
            kitsList: [],
        }
    }
    

    componentDidMount() {  

      fetch('https://spartt.github.io/json/kitsData.json')
      .then(resp => resp.json())
      .then(
        (result) => {
          this.setState({
            kitIsLoaded: true,
            kitsList: result
          });
        },
        (error) => {
          this.setState({
            kitIsLoaded: true,
            error
          });
        }
            
        )                 
    }

    closeTable(val) {
        val = '.' + val
        document.querySelector(val).style.display = 'none'
        document.querySelector(`.kit-${this.props.class} .kitDetails`).style.display = 'none'
    }

    renderProducts(param) {
        // param === caminho que leva até aos produtos. Ex: productList.inversor.disponiveis, onde .inversor.disponiveis é o param 

        const { error, kitIsLoaded, kitsList  } = this.state 
        //console.log(productList)
        if(error) {
            return(<tr><td>Error: {error.message}</td></tr>)
        }else if(!kitIsLoaded) {
            return <tr><td colSpan="2">Loading...</td></tr>;
        }else {
            var selectedList
            switch(param) {
                case 'gridTie':
                    selectedList = kitsList.gridTie
                break;
                case 'offGrid':
                    selectedList = kitsList.offGrid
                break;
                case 'bombaKit':
                    selectedList = kitsList.bomba
                break;

                default:
                    console.log('parametro não encontrado')
                break;
            }   

                if(selectedList.length > 0) {
                    return(
                        selectedList.map(product => (
                        <tr onClick={() => this.selectKit(param, product.products, product.kitId)}  key={product.kitId}>
                            <td>{product.kitId}</td>
                            <td>{product.nome}</td>
                        </tr>
                        ))
                    )
                }else {
                    return(
                        <tr><td colSpan="2">Não foram encontrados kits nessa tabela</td></tr>
                    )
                }
        }
    }

    selectKit(param, productData, kitId) {

        var painel = []
        var inversor = []
        var controlador = []
        var bomba = []
        var bateria = []
        var stringBox = []
        var suporte = []
        var cabo = []
        var conector = []
        var extras = []

        productData.forEach(function(el, i) {
            
            if(el.nome.toLowerCase().search(/painel\ssolar\s/g) > -1 && el.nome.toLowerCase().search(/suporte/g) === -1 ){
                painel.push(el.nome)																												       
            }else if ( (el.nome.toLowerCase().search(/inversor/g) > -1 && el.nome.toLowerCase().search(/grid-tie/g) > -1 ) 
            || el.nome.toLowerCase().search(/micro\sinversor\sgrid-tie/g) > -1  ) {

                inversor.push(el.nome)
            }else if(el.nome.toLowerCase().search(/string/g) > -1) {
                stringBox.push(el.nome)
            }else if (el.nome.toLowerCase().search(/^suporte/g) > -1 || el.nome.toLowerCase().search(/telha/g) > -1 ) {
                suporte.push(el.nome)
            }else if(el.nome.toLowerCase().search(/cabo/g) > -1 && el.nome.toLowerCase().search(/microinversor/g) === -1) {
                cabo.push(el.nome)
            }else if(el.nome.toLowerCase().search(/conector/g) >-1) {
                conector.push(el.nome)
            }else if(el.nome.toLowerCase().search(/controlador\sde\scarga/g) >-1 ) {
                controlador.push(el.nome)
            }else if(el.nome.toLowerCase().search(/bateria/g) >-1 ) {
                bateria.push(el.nome)
            }else if(el.nome.toLowerCase().search(/bomba\ssolar/g) >-1 ) {
                bomba.push(el.nome)
            }else {
                extras.push(el.nome)
            }

        })
        this.setState({
            kitPainel: painel,
            kitSuporte: suporte,
            kitCabo: cabo,
            kitConector: conector,
            kitExtras: extras,
            kitId: kitId    
        })
        switch(param) {
            case 'gridTie':
                this.setState({
                    kitTipo: 'grid-tie',
                    kitInversor: inversor,
                    kitStringBox: stringBox,
                })
            break;
            case 'offGrid':
                this.setState({
                    kitPainel: painel,
                    kitTipo: 'off-grid',
                    kitControlador: controlador,
                    kitInversor: inversor,
                    kitBateria: bateria,
                })
            break;
            case 'bombaKit':
                this.setState({
                    kitPainel: painel,
                    kitTipo: 'bomba-solar',
                    KitBomba: bomba,
                })
            break;
            default:
                console.log('parametro não encontrado')
            break;
        }
        document.querySelector(`.kit-${this.props.class} .kitDetails`).style.display = 'grid'
    }
    
    render() {
        return(
            <div className={`kit-container kit-${this.props.class}`}>
                <div className={`content ${this.props.class}`}>
                    <h2>{this.props.titulo}</h2>
                    <span onClick={() => this.closeTable(this.props.class)} className="closeBtn">X</span>
                        <table style={{marginTop: 4+'ch'}}>
                            <tbody>
                                <tr className="tableTitle"><th>Prod Id</th><th>Nome</th></tr>
                                {this.renderProducts(this.props.param, this.props.tipo)}
                            </tbody>
                        </table>
                </div>
                <KitDetails tipo={this.state.kitTipo} painel={this.state.kitPainel} inversor={this.state.kitInversor}
                 controlador={this.state.kitControlador} bomba={this.state.KitBomba} bateria={this.state.kitBateria}
                 stringBox={this.state.kitStringBox} suporte={this.state.kitSuporte} cabo={this.state.kitCabo}
                 conector={this.state.kitConector} extras={this.state.kitExtras}  id={this.state.kitId}/>
            </div>
        )
    }

}

