import React, { Component, Fragment } from 'react'
import './mainLayout.css'
import KitsTables from '../kitsTables.js'
import ProdTables from '../produtctsTable.js'
import Activatables from '../toActivate.js'
import EmFalta from '../kitsEmFalta.js'
/*
    * A class definida em ProdTables/KitsTables interfere na exibição/ocultação da tabela
    * "param" define o caminho até o json do tipo de produto. Ex: productList.inversor.disponiveis, onde .inversor.disponiveis é o param 

*/

export default class Products extends Component {

    render() {
        return(
            <Fragment>         
                <div className="container">
                    <EmFalta/>
                    <KitsTables titulo="Grid-Tie" param="gridTie" class="grid-tie" />
                    <KitsTables titulo="Off-Grid" param="offGrid" class="off-grid" />
                    <KitsTables titulo="Bomba-Solar" param="bombaKit" class="bomba" />
                    <ProdTables titulo="Paineis Disponiveis" param="painel.disponiveis" class="painel-disponivel" />
                    <ProdTables titulo="Paineis Indisponiveis" param="painel.indisponiveis" class="painel-indisponivel" />
                    <ProdTables titulo="Inversores Grid-Tie Disponiveis" param="inversorGT.disponiveis" class="inversorGT-disponivel" />
                    <ProdTables titulo="Inversores Grid-Tie Indisponiveis" param="inversorGT.indisponiveis" class="inversorGT-indisponivel" />
                    <ProdTables titulo="Inversores Off-Grid Disponiveis" param="inversorOG.disponiveis" class="inversorOG-disponivel" />
                    <ProdTables titulo="Inversores Off-Grid Indisponiveis" param="inversorOG.indisponiveis" class="inversorOG-indisponivel" />
                    <ProdTables titulo="Inversores Disponiveis" param="controlador.disponiveis" class="controlador-disponivel" />
                    <ProdTables titulo="Inversores Indisponiveis" param="controlador.indisponiveis" class="controlador-indisponivel" />
                    <ProdTables titulo="Bombas Disponiveis" param="bomba.disponiveis" class="bomba-disponivel" />
                    <ProdTables titulo="Bombas Indisponiveis" param="bomba.indisponiveis" class="bomba-indisponivel" />
                </div>
                <Activatables/>
            </Fragment>
        )
    }
}

